.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* App.css */
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  overflow: hidden;
}


.container {
  max-width: 600px;
  width: 90%;
  min-height: 100vh;
  margin: 0 auto;
  padding: 2rem 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  color: #e6e6e6;
  border: 1px solid rgba(255, 215, 0, 0.1);
}

h1 {
  text-align: center;
  color: #ffd700;
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  text-shadow: 0 2px 4px rgba(255, 215, 0, 0.3);
  font-family: 'Orbitron', sans-serif;
  width: 100%;
}

.status-board {
  width: 100%;
  background: rgba(255, 215, 0, 0.05);
  padding: 1.2rem;
  border-radius: 15px;
  margin: 1.5rem 0;
  border: 1px solid rgba(255, 215, 0, 0.1);
  backdrop-filter: blur(5px);
}

.status-board div {
  margin: 0.8rem 0;
  font-size: 1.1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}

.betting-panel, .cashout-panel {
  width: 100%;
  margin: 1.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 480px) {
  .container {
    width: 100%;
    border-radius: 0;
    padding: 1.5rem 1rem;
  }

  h1 {
    font-size: 1.8rem;
  }

  .status-board {
    padding: 1rem;
    margin: 1rem 0;
  }

  .status-board div {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
    font-size: 1rem;
  }

  input[type="number"] {
    width: 100%;
    margin: 0 0 1rem 0;
  }

  button {
    width: 100%;
    padding: 12px;
    font-size: 14px;
  }
}

input[type="number"] {
  padding: 12px;
  width: 200px;
  font-size: 16px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #ffd700;
  border-radius: 8px;
  color: #ffd700;
  text-align: center;
  -webkit-appearance: none;
  margin: 0 0 1rem 0;
}

button {
  min-width: 200px;
  padding: 14px 28px;
  font-size: 16px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.confiscate-message {
  width: 100%;
  text-align: center;
  background: rgba(255, 68, 68, 0.1);
  border: 2px solid #ff4444;
  border-radius: 15px;
  padding: 1.2rem;
  margin-top: 1.5rem;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: scale(0.98); }
  50% { transform: scale(1.0); }
  100% { transform: scale(0.98); }
}

@keyframes mass-pulse {
  0% { transform: scale(0.97); }
  50% { transform: scale(1.3); }
  100% { transform: scale(0.97); }
}

.container::before {
  content: "₿";
  position: fixed;
  font-size: clamp(80px, 20vw, 120px);
  opacity: 0.05;
  right: 10px;
  top: 20px;
  z-index: -1;
}

@media (max-width: 480px) {
  .status-board div {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 0.5rem;
  }

  input[type="number"] {
    width: 100%;
    max-width: none;
  }

  button {
    width: 100%;
    max-width: none;
  }
}

/* 按钮特效保持 */
.betting-panel button {
  background: linear-gradient(45deg, #00c853, #009624);
  color: #fff;
}

.cashout-panel button {
  background: linear-gradient(45deg, #ff9100, #ff6d00);
}

button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
}

.result-popup {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(45deg, #28a745, #20c997);
  color: white;
  padding: 15px 30px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  animation: slideDown 0.5s ease-out, fadeOut 0.5s ease-out 9.5s;
  font-size: 1.2rem;
  font-weight: bold;
}

.result-popup.profit {
  background: linear-gradient(45deg, #28a745, #20c997);
}

.result-popup.loss {
  background: linear-gradient(45deg, #dc3545, #c82333);
}

@keyframes slideDown {
  from {
    transform: translate(-50%, -100%);
    opacity: 0;
  }
  to {
    transform: translate(-50%, 0);
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.balloon {
  animation-iteration-count: infinite;
  animation-duration: 25s;
  animation-direction: alternate;
  animation-name: balloonize;
  font-weight: bold;
}

@keyframes balloonize {
  from {
    font-size: 1rem;
  }

  to {
    font-size: 5rem;
  }
}

@keyframes transform {
  from {
    transform:scale(.1);
  }
  to {
    transform:scale(1);
  }
}

/* bet slider */
.bet-slider {
  width: 100%;
  margin: .5rem;
  margin-bottom: 2rem;
  -webkit-appearance: none;
  appearance: none;
  height: 10px;
  background: rgba(255, 215, 0, 0.1);
  border-radius: 5px;
  outline: none;
  border: 1px solid rgba(255, 215, 0, 0.3);
}

.bet-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: #ffd700;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
}

.bet-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #ffd700;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
}

.bet-slider::-webkit-slider-thumb:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

.bet-slider::-moz-range-thumb:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

/* balance tracking */
.balance-decrease {
  animation: balanceDecrease 0.5s ease;
  color: #ff4444;
}

.balance-increase {
  animation: balanceIncrease 0.5s ease;
  color: #44ff44;
}

@keyframes balanceDecrease {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
    color: #ff0000;
  }
  100% {
    transform: scale(1);
  }
}

@keyframes balanceIncrease {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
    color: #00ff00;
  }
  100% {
    transform: scale(1);
  }
}